import "core-js/modules/es.array.push.js";
import { onMounted, reactive, toRefs, computed } from "vue";
import { useStore } from "vuex";
import ListItem from "../../../components/ListItem.vue";
import { showToast, showFailToast } from "vant";
import { useRouter } from "vue-router";
export default {
  components: {
    ListItem
  },
  props: ["changeShow"],
  setup(props) {
    const store = useStore();
    const router = useRouter();
    const data = reactive({
      checked: [],
      submitChecked: true
    });
    // const handleAdd = () => {};

    const handlechange = (value, detail) => {
      store.state.cartList.map(item => {
        if (item.id === detail.name) {
          item.num = value;
        }
      });
    };
    const init = () => {
      data.checked = store.state.cartList.map(item => item.id);
    };
    const onSubmit = () => {
      if (data.checked.length) {
        store.commit("PAY", updateData());
        router.push({
          path: "/createorder",
          query: {
            list: data.checked
          }
        });
      } else {
        showFailToast("请选择");
      }
    };
    const choseAll = () => {
      if (data.checked.length !== store.state.cartList.length) {
        init();
      } else {
        data.checked = [];
      }
    };
    const groupChange = result => {
      // console.log(a);
      if (result.length === store.state.cartList.length) {
        data.submitChecked = true;
      } else {
        data.submitChecked = false;
      }
      data.checked = result;
    };
    const allPrice = computed(() => {
      let countList = updateData();
      let sum = 0;
      countList.forEach(item => {
        sum += item.num * item.price;
      });
      return sum * 100;
    });
    const updateData = type => {
      return store.state.cartList.filter(item => {
        return type === "DELETE" ? !data.checked.includes(item.id) : data.checked.includes(item.id);
      });
    };
    const handleDelete = () => {
      // 部分删除 //判断data的checked是否有值
      if (data.checked.length) {
        store.commit("DELETE", updateData("DELETE"));
        data.checked = [];
        // 购物车没有数据的时候
        if (!store.state.cartList.length) {
          props.changeShow();
          store.commit("EDIT", "DELETE");
        }
      } else {
        showToast("请选择要删除的商品");
      }
      // 全部删除
    };

    onMounted(() => {
      init();
    });
    return {
      ...toRefs(data),
      store,
      handlechange,
      onSubmit,
      choseAll,
      groupChange,
      allPrice,
      handleDelete
    };
  }
};